<template>
    <div id="edit-employee">
    <h2>Edit Employee</h2>
     <div class="row">
    <form @submit.prevent="updateEmployee" class="col s12">
    <div class="row">
    <input disabled type="text" v-model="employee_id" required>
    </div>

    <div class="row">
    <input type="text" v-model="name" required>
    </div>

    <div class="row">
    <input type="text" v-model="dept" required>
    </div>

    <div class="row">
    <input type="text" v-model="position" required>
    </div>
    <button type="submit" class="btn">Submit</button>
    <router-link to="/" class="btn grey">Cancel</router-link>

    </form>
    </div>
    </div>
</template>
<script>
import db from './firebaseInit'
export default {
    name:'edit-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            position:null,
            dept:null
        }
    },
    beforeRouteEnter (to, from, next) {
       // ...
       db.collection('employees').where('employee_id','==',to.params.employee_id)
       .get().then(querySnapShot=>{
           querySnapShot.forEach(doc=>{
               next(vm=>{
                   vm.employee_id=doc.data().employee_id,
                   vm.name=doc.data().employee_name,
                   vm.dept=doc.data().employee_dept,
                   vm.position=doc.data().employee_position
               })
           })
       })
   },
   watch:{
       '$route':'fetchData'
   },
   methods:{
       fetchData(){
           db.collection('employees').where('employee_id','==',this.$route.params.employee_id)
           .get().then(querySnapShot=>{
               querySnapShot.forEach(doc=>{
                   this.employee_id=doc.data().employee_id

                   this.name=doc.data().employee_name

                   this.dept=doc.data().employee_dept
                   this.position=doc.data().employee_position
               })
           })
       },
       updateEmployee(){
            db.collection('employees').where('employee_id','==',this.$route.params.employee_id)
           .get().then(querySnapShot=>{
               querySnapShot.forEach(doc=>{
                  doc.ref.update({
                      employee_id:this.employee_id,
                      employee_name:this.name,
                      employee_dept:this.dept,
                      employee_position:this.position

                  })
                  .then(()=>{
                      this.$router.push({name:'view-employee',
                      params:{employee_id:this.employee_id}}
                      )
                  })
               })
           })
       }
   }
}
</script>


