<template>
    <div id="dashboard">
    <ul class="collection with-header">
        <li class="collection-header">
        <h4>Employees</h4>
        </li>
        <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
           <div class="chip">{{employee.dept}}</div>
           {{employee.employee_id}}:{{employee.name}}

           <router-link class="secondary-content" v-bind:to="{name:'view-employee',params:{employee_id:employee.employee_id}}">
              <i class="fa fa-eye"></i>
           </router-link>
        </li>
    </ul>
    <div class="fixed-action-btn">
       <router-link class="btn-large red btn-floating" to="/new">
         <i class="fa fa-plus"></i>
       </router-link>
    </div>
    </div>
</template>
<script>
import db from './firebaseInit.js'
export default {
    name:'dashboard',
    data(){
        return{
            employees:[]
        }
    },
    created(){
         db.collection('employees').orderBy("employee_dept").get()
         .then(querySnapShot=>{
             querySnapShot.forEach(doc=>{
                
                 const data ={
                    'id':doc.id,
                    'employee_id':doc.data().employee_id,
                    'name':doc.data().employee_name,
                    'dept':doc.data().employee_dept,
                    'position':doc.data().employee_position
                 }

                 this.employees.push(data);
             })
         })
    }
}
</script>


