<template>
    <div id="view-employee">
    <ul class="collectiion header">
    <li class="collection-header">
    <h4>{{name}}</h4>
    </li>

     <li class="collection-item">Employee ID#:{{employee_id}}</li>
     <li class="collection-item">Employee Department#:{{dept}}</li>
     <li class="collection-item">Employee Position#:{{position}}</li>

    </ul>

    <router-link class="btn grey" to="/">Back</router-link>
    <button class="btn red" @click="deleteEmployee">Delete</button>
     <div class="fixed-action-btn">
       <router-link v-bind:to="{name:'edit-employee',params:{employee_id:employee_id}}" class="btn-large red btn-floating" >
         <i class="fa fa-pencil"></i>
       </router-link>
    </div>
    </div>
    
</template>
<script>
import db from './firebaseInit'
export default {
    name:'view-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            dept:null,
            position:null,
        }
    },
   beforeRouteEnter (to, from, next) {
       // ...
       db.collection('employees').where('employee_id','==',to.params.employee_id)
       .get().then(querySnapShot=>{
           querySnapShot.forEach(doc=>{
               next(vm=>{
                   vm.employee_id=doc.data().employee_id,
                   vm.name=doc.data().employee_name,
                   vm.dept=doc.data().employee_dept,
                   vm.position=doc.data().employee_position
               })
           })
       })
   },
   watch:{
       '$route':'fetchData'
   },
   methods:{
       fetchData(){
           db.collection('employees').where('employee_id','==',this.$route.params.employee_id)
           .get().then(querySnapShot=>{
               querySnapShot.forEach(doc=>{
                   this.employee_id=doc.data().employee_id

                   this.name=doc.data().employee_name

                   this.dept=doc.data().employee_dept
                   this.position=doc.data().employee_position
               })
           })
       },
       deleteEmployee(){
           if(confirm('Are you sure?')){
               db.collection('employees').where('employee_id','==',this.$route.params.employee_id)
           .get().then(querySnapShot=>{
               querySnapShot.forEach(doc=>{
                   doc.ref.delete();
                   this.$router.push('/');
               })
           })
           }
       }
   }
}
</script>


